html, body {
    height: 100%;
    background: url("./images/girl_with_computer.jpg") no-repeat;
    background-size: cover;
}
.App {
    height: 100%;
    padding: 30px;
    display: flex;
}
.task {
    background-color: rgba(0, 0, 0, 0.5);
    padding: 10px;
    border-radius: 10px;
    color: #fff;
}

.App > div {

    margin-right: 50px;
}

.isDone {
    opacity: 0.5;
}

.selected {
    background-color: gold;
}
.error {
    border: 2px solid lightcoral;
    outline: transparent;
}
.errorMessage {
    color: red;
}